// visual_workflow_editor/frontend/src/App.tsx
import React from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { Container, Box } from '@mui/material';
import FlowEditorWrapper from './components/FlowEditor';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { CssBaseline } from '@mui/material';
import { useTranslation } from 'react-i18next';
import { SnackbarProvider } from 'notistack';
// 
import Register from './components/Register';
import Login from './components/Login';
import Submit from './components/Submit';
import ProtectedRoute from './components/ProtectedRoute';
import NavBar from './components/NavBar';

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
  },
});

/**
 * App Component
 *
 * This is the main application component that sets up routing and the overall layout.
 */
const App: React.FC = () => {
  const { t } = useTranslation();

  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <SnackbarProvider maxSnack={3}>
        <Router>
          <NavBar />
          <Container maxWidth="xl" sx={{ mt: 2 }}>
            <Routes>
              {/*  */}
              <Route path="/" element={<Navigate to="/login" replace />} />
              
              {/*  -  */}
              <Route path="/register" element={<Register />} />
              <Route path="/login" element={<Login />} />
              <Route path="/submit" element={<Submit />} />
              
              {/*  -  */}
              <Route path="/flow" element={
                <ProtectedRoute>
                  <FlowEditorWrapper />
                </ProtectedRoute>
              } />
              <Route path="/flow/:flowId" element={
                <ProtectedRoute>
                  <FlowEditorWrapper />
                </ProtectedRoute>
              } />
              
              {/*  */}
              <Route path="*" element={<Navigate to="/login" replace />} />
            </Routes>
          </Container>
        </Router>
      </SnackbarProvider>
    </ThemeProvider>
  );
};

export default App;