// visual_workflow_editor/frontend/src/components/LanguageSelector.tsx
import React, { useState, useEffect } from 'react';
import { Button, Menu, MenuItem } from '@mui/material';
import LanguageIcon from '@mui/icons-material/Language';
import { useTranslation } from 'react-i18next';

/**
 * LanguageSelector Component
 * 
 * Allows users to switch between different languages in the application.
 */
const LanguageSelector: React.FC = () => {
  const { i18n } = useTranslation();
  const [anchorEl, setAnchorEl] = useState<HTMLElement | null>(null);
  const open = Boolean(anchorEl);

  // effect
  useEffect(() => {
    // documentlang
    document.documentElement.lang = i18n.language;
    
    // HTML
    const title = document.querySelector('title');
    if (title) {
      title.textContent = i18n.language === 'zh' ? '' : 'Visual Workflow Editor';
    }
  }, [i18n.language]);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const changeLanguage = (lng: string) => {
    i18n.changeLanguage(lng);
    localStorage.setItem('preferredLanguage', lng); // 
    handleClose();
    
    // 
    window.dispatchEvent(new Event('languageChanged'));
  };

  // 
  useEffect(() => {
    const savedLanguage = localStorage.getItem('preferredLanguage');
    if (savedLanguage && savedLanguage !== i18n.language) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n]);

  return (
    <>
      <Button
        color="inherit"
        startIcon={<LanguageIcon />}
        onClick={handleClick}
        aria-label="select language"
      >
        {i18n.language === 'zh' ? '' : 'English'}
      </Button>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
      >
        <MenuItem onClick={() => changeLanguage('zh')}></MenuItem>
        <MenuItem onClick={() => changeLanguage('en')}>English</MenuItem>
      </Menu>
    </>
  );
};

export default LanguageSelector;